import React from "react";

function Comment({img="", name, comment, time}) {
  return (
    <div className="comment">
      <hr />
      <div className="row">
        <div
          className="col-2 col-lg-2 col-md-2 col-sm-2 col-xs-2 commentPicture"
          style={{ textAlign: "center" }}
        >
          <i class="far fa-user-circle fa-3x"></i>
        </div>
        <div className="col-7 col-lg-7 col-md-7 col-sm-7 col-xs-7 mx-lg-1 pl-lg-0 mx-md-1 pl-md-0 mx-sm-2 pl-sm-1 mx-xs-2 pl-xs-1 commentText">
          <h6>{name}</h6>
          <p className="postColorZrey">{comment}</p>
        </div>
        <div className="col  commentTime" style={{ textAlign: "center" }}>
          <span className="postColorZrey">{time}</span>
        </div>
      </div>
    </div>
  );
}

export default Comment;
