import React from "react";
import "./new-post.styles.css";

function NewPost({ newPostAction, setNewPostAction }) {
  return (
    <div className="newPost">
      <div
        className="jumbotron pt-2 pb-0 px-0 "
        style={{ backgroundColor: "white" }}
      >
        <div className="row py-3">
          <div className="col-1 col-lg-1 col-sm-1 col-md-1 "></div>
          <div
            className="col-3  col-sm-3 col-md-3 small-font hover border-zrey"
            onClick={() => setNewPostAction("Share An Update")}
            style={
              newPostAction === "Share An Update"
                ? {
                    border: "solid",
                    borderWidth: "0 0 2px 0",
                    borderColor: "#0077B5",
                  }
                : {}
            }
          >
            <a href="#" id="Share An Update" style={{ color: "#C6C7C9" }}>
              <i
                class="fas fa-pencil-alt"
                style={{
                  color: "#84C0D9",
                  position: "relative",
                  right: "5px",
                }}
              ></i>
              <span style={{ fontSize: "13px" }}>Share An Update</span>
            </a>
          </div>
          <div
            className="col-3  col-sm-3 col-md-3 small-font hover border-zrey"
            onClick={() => setNewPostAction("Upload A Photo")}
            style={
              newPostAction === "Upload A Photo"
                ? {
                    border: "solid",
                    borderWidth: "0 0 2px 0",
                    borderColor: "#0077B5",
                  }
                : {}
            }
          >
            <a href="#" id="Upload A Photo" style={{ color: "#C6C7C9" }}>
              <i
                class="far fa-image"
                style={{ color: "#F2E081", position: "relative", right: "5px" }}
              ></i>
              Upload A Photo
            </a>
          </div>
          <div
            className="col-3  col-sm-3 col-md-3 small-font hover border-zrey"
            onClick={() => setNewPostAction("Write An Article")}
            style={
              newPostAction === "Write An Article"
                ? {
                    border: "solid",
                    borderWidth: "0 0 2px 0",
                    borderColor: "#0077B5",
                  }
                : {}
            }
          >
            <a href="#" id="Write An Article" style={{ color: "#C6C7C9" }}>
              <i
                class="fas fa-book-open"
                style={{ color: "#42BD80", position: "relative", right: "5px" }}
              ></i>
              Write An Article
            </a>
          </div>
          <div className="col-3 col-lg-1 col-sm-1 col-md-1 "></div>
        </div>
      </div>
      <div
        className="row mb-3 p-auto"
        style={{ width: "95%", position: "relative", left: "5%" }}
      >
        <input
          className="col-12 m-auto inputText"
          type="text"
          placeholder="Write Something..."
        ></input>
      </div>
    </div>
  );
}

export default NewPost;
