import React from "react";
import "./posted-post.styles.css";
import Comment from "./comment.component";

function PostedPost({ name, designation, time, commentText, img }) {
  function handleClick() {
    document.getElementById("heart").style.color = "red";
  }
  return (
    <div className="postedPost">
      <div className="container-fluid p-3">
        <div className="row p-0 m-0">
          {/* porfili detail */}
          <div className="col-12 col-lg-12 col-md-12 col-sm-12 col-xs-12 p-0 m-0">
            <div className="row">
              <div
                className="col-2 col-lg-2 col-sm-2 col-md-2 col-xs-2  postProfilePicture"
                style={{ textAlign: "center" }}
              >
                <i class="far fa-user-circle fa-3x"></i>
              </div>
              <div className="col-6 col-lg-6 col-md-6 col-sm-6 col-xs-6  mx-lg-1 pl-lg-0 mx-md-1 pl-md-0 mx-sm-2 pl-sm-1 mx-xs-2 pl-xs-1">
                <h6 className="postName">{name}</h6>
                <p className="postDesignation postColorZrey">{designation}</p>
              </div>
              <div
                className="col-3 col-lg-3 col-md-3 col-sm-3 col-xs-3 my-auto  postTime"
                style={{ textAlign: "center" }}
              >
                <span className="postColorZrey ">{time}</span>
              </div>
            </div>
          </div>
          <br />
          {/* post text */}
          <div className="col-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <div className="row">
              <div className="col-12  pb-1 my-3 postText postColorZrey">
                {commentText}
              </div>
            </div>
          </div>
          <br />
          {/* postImage */}
          <div className="col-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
            <div className="row">
              <div className="col-12  pb-1 my-3 postImage">
                <img src = {img} style={{width: "100%", height:"300px"}}/>
              </div>
            </div>
          </div>
          <br />
          {/* like cmnt share */}
          <div className="col-12 col-lg-12 col-md-12 col-sm-12 col-xs-12 p-0 m-0">
            <div className="row p-0 m-0">
              <div className="col-12  likeCmntShare">
                <div className="row" style={{ textAlign: "center" }}>
                  <div className="col " onClick={handleClick}>
                    <i class="far fa-heart postColorZrey" id="heart"></i>
                  </div>
                  <div className="col">
                    <i class="far fa-comment postColorZrey"></i>
                  </div>
                  <div className="col">
                    <i class="fas fa-share-alt postColorZrey"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <br />
        {/* cmnt section */}
        <Comment
          name="Shreyansh Kumar Lal"
          comment="I love Coding!!!!!!!!"
          time="5 min ago"
        />
        <hr />
        <div className="row p-3">
          <div className="col-12 col-lg-12 col-md-12 col-sm-12 input-group">
            <input
              type="text"
              placeholder="Add a Comment"
              className="form-control form-secondary addComment"
              ariaLabel="Sizing example input"
              ariaDescribedby="inputGroup-sizing-default"
            />
            <div className="input-group-append">
              <button className="btn btn-outline-secondary" type="button">
                Post
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PostedPost;
